.btn {
  color: #111;
  background: linear-gradient(141.22deg, #ffc226 9.4%, #f84119 91.91%);
  border: rgba(0, 0, 0, 0);
  border-radius: 74px;
  padding: 10px 24px;
  font-weight: 500;
  transition: color .25s cubic-bezier(.4, 0, .2, 1), border .25s cubic-bezier(.4, 0, .2, 1);
}

.btn:hover, .btn:focus {
  color: #f87719;
  background: #fff;
  border: 1px solid #f87719;
}

.btn-search {
  color: #111;
  background: linear-gradient(141.22deg, #ffc226 9.4%, #f84119 91.91%);
  border: rgba(0, 0, 0, 0);
  border-radius: 50%;
  transition: color .25s cubic-bezier(.4, 0, .2, 1), border .25s cubic-bezier(.4, 0, .2, 1);
}

.btn-search:hover, .btn-search:focus {
  color: #f87719;
  background: #fff;
  border: 1px solid #f87719;
}

