  color: #fff;
  text-transform: uppercase;
  font-size: 14px;
  line-height: 1.14;
}

.menu-close-btn {
  background-color: rgba(0, 0, 0, 0);
  border: none;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  transform: translate(0);
}

.svg-close {
